@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);

.header-container {
  background: #262734;
  position: fixed;
  width: 100%;
  z-index: 100;
  top: 0;
  box-shadow: 1px 2px 5px #2c2d3b; }
  .header-container .container .main-menu {
    color: aliceblue; }

.logo-div {
  padding: 10px; }
  .logo-div i.menu-icon {
    font-size: 25px;
    cursor: pointer; }
  .logo-div .logo {
    margin-left: 20px; }
    .logo-div .logo i {
      position: relative;
      font-size: 40px;
      top: 8px; }
    .logo-div .logo .logo-text {
      position: relative;
      top: -8px;
      margin-left: 8px;
      font-size: 15px; }

ul.menu-items {
  list-style: none;
  position: relative;
  top: 9px;
  margin-bottom: 9px; }
  ul.menu-items a {
    color: inherit; }
    ul.menu-items a:hover {
      text-decoration: none; }
  ul.menu-items li {
    font-size: 18px;
    display: inline-block;
    padding: 15px 30px 20px 30px;
    cursor: pointer;
    width: 30%;
    text-align: center;
    margin-right: 4px; }
    ul.menu-items li:hover {
      border-bottom: 5px solid #2869bc;
      transition: 0.3s; }
  ul.menu-items li.active {
    border-bottom: 5px solid #2869bc; }

.login-section {
  border-left: 1px solid #32333f;
  margin: 5px 0px; }
  .login-section span {
    float: right;
    margin-top: 10px; }
    .login-section span button {
      border: none;
      padding: 10px 13px;
      font-size: 15px; }
    .login-section span button.btn-add {
      padding-left: 8px;
      background: #2869bc;
      color: aliceblue;
      outline: none;
      border-radius: 2px; }
      .login-section span button.btn-add i {
        font-size: 25px;
        vertical-align: middle;
        margin-right: 5px; }
    .login-section span img.avatar-img {
      width: 45px;
      border-radius: 50%;
      margin-left: 30px; }
    .login-section span button.btn-login {
      margin-top: 2px;
      background: none;
      color: aliceblue;
      margin-right: 20px;
      outline: none; }
    .login-section span button.btn-signup {
      margin-top: 2px;
      color: #2869bc;
      padding-left: 25px;
      padding-right: 25px;
      border-radius: 20px; }

.login-tran-enter {
  opacity: 0.01; }

.login-tran-enter.login-tran-enter-active {
  opacity: 1;
  transition: opacity 300ms ease-in; }

.login-tran-leave {
  opacity: 1; }

.login-tran-leave.login-tran-leave-active {
  opacity: 0.01;
  transition: opacity 300ms ease-in; }

.container {
  position: relative; }

.login-form {
  background: #262734;
  width: 50%;
  margin: auto;
  padding: 50px 80px 65px 80px;
  position: relative; }
  .login-form form {
    margin-top: 20px; }
    .login-form form input[type='text'],
    .login-form form input[type='password'] {
      width: 100%;
      background: #363748;
      border: none;
      padding: 20px;
      margin-bottom: 25px;
      outline: none;
      color: #b5b5b5; }
    .login-form form .btn-login {
      outline: none;
      border: none;
      background: #2869bc;
      color: aliceblue;
      width: 100%;
      padding: 20px; }
  .login-form .logo {
    display: block;
    text-align: center;
    color: aliceblue; }
    .login-form .logo i {
      font-size: 40px; }
    .login-form .logo .logo-text {
      position: relative;
      top: -14px;
      margin-left: 10px; }
  .login-form .btn-close {
    color: #b5b5b5;
    position: absolute;
    right: 30px;
    top: 25px;
    cursor: pointer; }
  .login-form .forgot-pass-legend {
    margin-top: 15px;
    font-size: 14px;
    color: #b5b5b5; }
    .login-form .forgot-pass-legend a {
      color: #2869bc; }
      .login-form .forgot-pass-legend a:hover {
        text-decoration: none;
        color: #2869bc; }
    .login-form .forgot-pass-legend .recover-pass {
      color: #b5b5b5;
      float: right;
      cursor: pointer; }

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.8);
  z-index: 200; }
  .modal-overlay .modal-content {
    position: absolute;
    top: 50%;
    -webkit-transform: translateY(-50%);
            transform: translateY(-50%);
    background: none;
    box-shadow: 2px 2px 10px #000000; }

.movie-tile {
  background: #262734;
  color: aliceblue;
  position: relative;
  padding: 15px;
  box-shadow: 1px 2px 10px #252525;
  margin-top: 100px;
  border-radius: 2px; }
  .movie-tile .popularity {
    position: absolute;
    right: 15px;
    top: 25px;
    color: #59d28e;
    font-size: 11px;
    font-weight: bold;
    padding-bottom: 3px; }
    .movie-tile .popularity .bottom-border {
      position: absolute;
      border: 1px solid #59d28e;
      bottom: 0px;
      width: 8px;
      left: 20%; }
  .movie-tile i.ellipsis {
    position: absolute;
    right: 5px;
    top: 5px;
    font-size: 15px;
    cursor: pointer;
    color: #b5b5b5; }
  .movie-tile i.btn-like {
    position: absolute;
    right: 10px;
    bottom: 10px;
    font-size: 18px;
    cursor: pointer;
    color: #be1123; }
  .movie-tile img {
    width: 45%;
    display: inline-block;
    position: absolute;
    bottom: 25px;
    box-shadow: 1px 2px 10px #252525;
    border-radius: 2px; }
  .movie-tile .movie-details {
    padding-left: 52%;
    height: 180px; }
    .movie-tile .movie-details h4 {
      font-size: 20px;
      padding-right: 25px; }
    .movie-tile .movie-details .release-date {
      font-size: 11px;
      color: #b5b5b5; }
    .movie-tile .movie-details .description {
      margin-top: 10px;
      color: #b5b5b5;
      font-size: 13px; }

.loader-container {
  text-align: center;
  margin-top: 50px; }

.top-rated-movies {
  padding-top: 90px; }
  .top-rated-movies h3 {
    margin-top: 30px;
    color: aliceblue; }


.left-nav-container {
  position: fixed;
  top: 0px;
  left: 0px;
  width: 250px;
  height: 100%;
  z-index: 300;
  background: #262734;
  color: #b5b5b5;
  box-shadow: 1px 2px 5px #252525; }
  .left-nav-container i.left-nav-close {
    float: right;
    margin: 20px;
    cursor: pointer; }
  .left-nav-container ul.left-nav-menu {
    margin: 10px;
    margin-top: 60px;
    list-style: none; }
    .left-nav-container ul.left-nav-menu li {
      padding: 10px 20px;
      font-size: 16px;
      cursor: pointer; }
      .left-nav-container ul.left-nav-menu li a {
        color: #b5b5b5; }
        .left-nav-container ul.left-nav-menu li a:hover {
          text-decoration: none; }
      .left-nav-container ul.left-nav-menu li i {
        font-size: 19px;
        margin-right: 13px;
        position: relative;
        top: 3px; }

.row .icon-overlay i {
  position: absolute;
  color: #fff;
  right: 39px;
  top: 19px;
  color: #b5b5b5; }

.row .col-sm-6 button.btn-cancel {
  background: transparent;
  border: none;
  width: 100%;
  padding: 20px;
  color: #b5b5b5;
  outline: none; }

.txt-movie-desc {
  width: 100%;
  background: #363748;
  border: none;
  padding: 20px;
  margin-bottom: 25px;
  height: 150px;
  outline: none;
  color: #b5b5b5; }

.notif-container {
  border: none;
  width: 550px;
  position: fixed;
  top: 114px;
  right: 100px;
  color: #ffffff;
  font-size: 15px;
  padding: 15px 20px 20px 20px;
  box-shadow: 2px 2px 5px #2c2d3b; }
  .notif-container i.icon-notif {
    font-size: 20px;
    margin-right: 10px;
    position: relative;
    top: 5px; }
  .notif-container i.btn-close {
    float: right;
    position: absolute;
    top: 17px;
    right: 17px;
    cursor: pointer; }

.success {
  background: #58c090; }

.danger {
  background: #ed6e7e; }

* {
  margin: 0;
  padding: 0;
  font-family: 'Open Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

body {
  background-color: #363748 !important; }

